import React, {useState} from "react";

const RecordForm = ({numList, setNumList}) => {
    const [num, setNum] = useState(1);
    return (
        <div>
            <div>  : {num}</div>
            <button onClick={() => {setNum(num + 1)}}> </button>
            <button onClick={() => {setNum(num - 1)}}> </button>
            <button onClick={() => {setNum(0)}}> </button>
            <hr/>
            <button onClick={() => setNumList([...numList, num])}> </button>
            <button onClick={() => setNumList([])}> </button>
        </div>
    )
}

const RecordList = ({numList}) => {
    return (
        <div>
            <h2> </h2>
            {numList.length > 0 ? <div>
                <ul className="a">
                    {numList.map((num) => (
                        <li>{num}</li>
                    ))}
                </ul>
            </div> : <div> </div>}
        </div>
    )
}

const About = () => {
    const [numList, setNumList] = useState([]);
    const [text, setText] = useState("");
    const [edit, setEdit] = useState(false);
    const [url, setUrl] = useState("");

    let content = <div>
        {text} <button onClick={() => setEdit(true)}></button>
    </div>

    if(edit) {
        content = <div>
            <input type="text" value={text}
                   onChange={(e) => {
                       setText(e.target.value);
                   }}/>
            <button onClick={() => setEdit(false)}></button>
        </div>
    }

    return (
        <div>
            <h1>  </h1>

            <div style={ {margin: "40px auto", width: "800px", textAlign: "center"} }>
                <RecordForm numList={numList} setNumList={setNumList}/>
                <RecordList numList={numList}/>
            </div>
            {content}
        </div>
    )
}

export default About;